<!--  -->
<template>
  <div>
    <el-row type="flex" :gutter="20">
      <el-col :span="8">
        <el-input v-model="name" placeholder=""></el-input>
      </el-col>
      <el-col>
        <el-button type="primary" @click="addClassify()"></el-button>
      </el-col>
    </el-row>
    <el-table :data="tableData" style="width: 100%;margin-top:20px;">
      <el-table-column prop="id" label="id" width="350"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column label="" width="200">
        <template slot-scope="scope">
          <el-button @click="handleUpdate(scope.$index, tableData)" type="text" size="medium"></el-button>
          <el-button @click="handleDelete(scope.$index, tableData)" type="text" size="medium"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import {
  addClassify,
  upateClassify,
  deleteClassify,
  queryClassify
} from "@/api/api";
export default {
  data() {
    return {
      name: "",
      tableData: [
        
      ]
    };
  },

  components: {},

  computed: {},

  mounted() {
      queryClassify().then(res=>{
          this.tableData = res.data;
      });
  },

  methods: {
    addClassify() {
      if (this.name) {
        var data = { name: this.name };
        addClassify(data)
          .then(res => {
            this.$message({
              message: "",
              type: "success"
            });
            data.id = res.data.id;
            this.tableData.push(data);
          })
          .catch(res => {
            this.$message.error(res.message);
          });
      } else {
        this.$message.error("");
      }
    },
    handleDelete(index, rows) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          console.log(rows);
          var classify = {};
          classify.id = rows[index].id;
          classify.name = rows[index].name;
          deleteClassify(classify)
            .then(res => {
              if (res.state == "0") {
                this.$message({
                  type: "success",
                  message: "!"
                });
                rows.splice(index, 1);
              }
            })
            .catch(res => {
              this.$message.error(res.message);
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    handleUpdate(index, rows) {
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        inputValue: rows[index].name
      })
        .then(({ value }) => {
          var classify = {};
          classify.id = rows[index].id;
          classify.name = value;
          upateClassify(classify)
            .then(res => {
              if (res.state == "0") {
                this.$message({
                  type: "success",
                  message: ""
                });
              }
              rows[index].name = value;
            })
            .catch(res => {
              this.$message({
                type: "error",
                message: ""
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  }
};
</script>
